/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import contactData from './data-bb-contact-http';

const contactDataModuleKey = 'data-bb-contact-http-ng';

export const contactDataKey = 'data-bb-contact-http-ng:contactData';

export default ng
  .module(contactDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(contactDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       contactData(config),
      ],
    };
  }])
  .name;



